import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import type { UseMutationResult } from 'react-query'
import { useMutation, useQuery, useQueryClient } from 'react-query'

import { COMMON_MESSAGE, MANAGE_RECRUITMENT_MESSAGE } from 'src/data/messages'

import type { ClubId } from 'src/models/Club'
import type { DateProps } from 'src/models/date'

import type { AddRecruitRequestData, GetRecruitResponseData } from 'src/responses/recruit'
import type { SearchRecruitsResponseBody } from 'src/responses/search'

interface GetRecruitParameters {
  clubId: ClubId
  date?: DateProps
  token: string
}

const getRecruitsAPICall =
  ({ clubId, date, token }: GetRecruitParameters) =>
  async () => {
    const response = await axios.get<GetRecruitResponseData>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/`,
      {
        params: date ?? null,
        headers: { Authorization: token }
      }
    )
    return response.data
  }

export const useGetRecruits = (clubId: ClubId, date?: DateProps) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  return useQuery<GetRecruitResponseData>(['recruits', date], getRecruitsAPICall({ clubId, date: date, token }), {
    onError: error => {
      if (error instanceof AxiosError) {
        enqueueSnackbar(MANAGE_RECRUITMENT_MESSAGE.NO_RECRUIT_MESSAGE, { variant: 'warning' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    },
    retry: 0
  })
}

interface PutRecruitParams {
  clubId: ClubId
  data: AddRecruitRequestData
  recruitId: number
}

const putRecruitAPICall = async ({ clubId, data, recruitId }: PutRecruitParams, token: string) => {
  await axios.put<null, AxiosResponse<null>, AddRecruitRequestData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/${recruitId}`,
    data,
    {
      headers: { Authorization: token }
    }
  )
}

export const useEditRecruit = (): UseMutationResult<void, unknown, PutRecruitParams, void> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = (values: PutRecruitParams) => putRecruitAPICall(values, token)

  return useMutation('putRecruit', mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.ADD_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('recruits')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.ADD_FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

interface PostRecruitParams {
  clubId: ClubId
  data: AddRecruitRequestData
  date: DateProps
}

const postRecruitAPICall = async ({ clubId, data, date }: PostRecruitParams, token: string) => {
  await axios.post<null, AxiosResponse<null>, AddRecruitRequestData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit?semester=${date.semester}&year=${date.year}`,
    data,
    {
      headers: { Authorization: token }
    }
  )
}

export const useCreateRecruit = (): UseMutationResult<void, unknown, PostRecruitParams, void> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = (values: PostRecruitParams) => postRecruitAPICall(values, token)

  return useMutation('postRecruit', mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.ADD_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('recruits')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.ADD_FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

export const getRecruitsByParams = (apiParams: URLSearchParams, token: string) => async () => {
  const response = await axios.get<SearchRecruitsResponseBody>(`${process.env.REACT_APP_APIHOST}/search/recruits`, {
    params: apiParams,
    headers: { Authorization: token }
  })
  return response.data
}

export const useGetRecruitsByParam = (searchParams: URLSearchParams) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const response = useQuery<SearchRecruitsResponseBody>(
    ['recruitsByParam', searchParams.toString()],
    getRecruitsByParams(searchParams, token),
    {
      onError: error => {
        if (!(error instanceof AxiosError)) {
          enqueueSnackbar('서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
            variant: 'error'
          })
        }
      }
    }
  )
  return response
}
