import { Link, useParams } from 'react-router-dom'

import AddTwoToneIcon from '@mui/icons-material/AddTwoTone'
import { Box, Button, Grid, Typography, useMediaQuery, useTheme } from '@mui/material'

import TabMenu from './TabMenu'

interface PageHeaderProps {
  clubName: string
  buttonText?: string
  secondButtonText?: string
  link?: string
  secondLink?: string
}

function PageHeader({ clubName, buttonText, secondButtonText, link, secondLink }: PageHeaderProps) {
  const clubId = useParams().id

  const theme = useTheme()
  const isDesktop = useMediaQuery(theme.breakpoints.up('md'))

  return (
    <Box paddingLeft={0} pr={0}>
      <Grid container alignItems="center">
        <Grid item xs={12} sm={4} md={4}>
          <Box display="flex" flexDirection={isDesktop ? 'row' : 'column'} alignItems={isDesktop ? 'center' : ''}>
            <Typography variant="h3" component="h3" marginRight="15px" gutterBottom>
              {clubName}
            </Typography>
            <Link to={`/club/${clubId}/profile`} style={{ marginBottom: '5px', textDecoration: 'none' }}>
              <Typography variant="body1" fontWeight={'bold'} color="primary">
                내 활동 바로가기
              </Typography>
            </Link>
          </Box>
          <Typography variant="subtitle2">{clubName}의 회원 명단, 모집•홍보, 활동 내역을 관리할 수 있습니다</Typography>
        </Grid>
        <Grid item xs={0} sm={0} md={4} />
        {secondButtonText ? (
          <Grid item xs={12} sm={4} md={2} textAlign="right">
            <Link to={secondLink ?? '#'} style={{ textDecoration: 'none' }}>
              <Button
                sx={{ mt: { xs: 2, md: 0 }, width: { xs: '100%', sm: 'inherit' } }}
                variant="contained"
                color="success"
              >
                {secondButtonText}
              </Button>
            </Link>
          </Grid>
        ) : (
          <Grid item xs={0} sm={4} md={2} />
        )}
        {buttonText && (
          <Grid item xs={12} sm={4} md={2} textAlign="center">
            <Link to={link ?? '#'} style={{ textDecoration: 'none' }}>
              <Button
                sx={{ mt: { xs: 2, md: 0 }, width: { xs: '100%', sm: 'inherit' } }}
                variant="contained"
                startIcon={<AddTwoToneIcon fontSize="small" />}
              >
                {buttonText}
              </Button>
            </Link>
          </Grid>
        )}
      </Grid>
      <TabMenu />
    </Box>
  )
}

export default PageHeader
