import { FormControl, Grid, InputLabel, MenuItem, Select, type SelectChangeEvent, TextField } from '@mui/material'
import { LocalizationProvider, MobileDateTimePicker } from '@mui/x-date-pickers'
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs'

import departments from 'src/data/departments'

import type { AddRecruitRequestData } from 'src/responses/recruit'

interface RecruitInfoProps {
  newRecruitInfo: AddRecruitRequestData
  handleChange: (name: string, value: Date | null | number | string) => void
}

const ITEM_HEIGHT = 48
const ITEM_PADDING_TOP = 8
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250
    }
  }
}

const currentYear = new Date().getFullYear()
const yearStart = new Date('2000').getFullYear()
const univEnteredYears = Array.from({ length: currentYear - yearStart + 1 }, (_, index) => {
  return { value: (currentYear - index - yearStart).toString().padStart(2, '0') }
})

const NO_LIMIT = '무관'

const RecruitInfo: React.FC<RecruitInfoProps> = ({ newRecruitInfo, handleChange }) => {
  const { title, allowedCollege, minGrade, maxGrade, startAt, endAt, limit } = newRecruitInfo

  const handleNullValueChange = (e: SelectChangeEvent<string>) => {
    if (e.target.value === NO_LIMIT) {
      return handleChange(e.target.name, null)
    } else return handleChange(e.target.name, e.target.value)
  }
  return (
    <Grid container direction="column" justifyContent="center" rowSpacing={2}>
      <Grid item display="flex">
        <TextField
          sx={{ paddingLeft: 0.5, paddingRight: 0.5 }}
          label="모집공고 제목"
          name="title"
          id="title"
          value={title}
          type="text"
          fullWidth
          onChange={e => handleChange(e.target.name, e.target.value)}
        />
      </Grid>
      <Grid container item sx={{ width: '100%' }} alignItems="center" rowSpacing={2}>
        <Grid item xs={12} sm={4} md={2} display="flex" justifyContent="center">
          <FormControl sx={{ width: '100%', paddingLeft: 0.5, paddingRight: 0.5 }} size="small" required>
            <InputLabel htmlFor="allowedCollege">모집 학과/학부</InputLabel>
            <Select
              id="allowedCollege"
              name="allowedCollege"
              label="모집 학과/학부"
              value={allowedCollege !== null ? allowedCollege : NO_LIMIT}
              onChange={handleNullValueChange}
              fullWidth
              MenuProps={MenuProps}
            >
              <MenuItem value={NO_LIMIT}>무관</MenuItem>
              {departments && Object.keys(departments).map(college => [<MenuItem value={college}>{college}</MenuItem>])}
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={6} sm={4} md={2} display="flex" justifyContent="space-between" alignItems="center">
          <FormControl sx={{ width: '100%', paddingLeft: 0.5, paddingRight: 0.5 }} size="small" required>
            <InputLabel htmlFor="minGrade">모집 최저 학번</InputLabel>
            <Select
              label="모집 최저 학번"
              name="minGrade"
              id="minGrade"
              value={minGrade !== null ? minGrade : NO_LIMIT}
              fullWidth
              onChange={handleNullValueChange}
              MenuProps={MenuProps}
            >
              <MenuItem value={NO_LIMIT}>무관</MenuItem>
              {univEnteredYears.map(option => (
                <MenuItem key={option.value} value={option.value}>
                  {option.value} 학번
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={6} sm={4} md={2} display="flex" justifyContent="space-between" alignItems="center">
          <FormControl sx={{ width: '100%', paddingLeft: 0.5, paddingRight: 0.5 }} size="small" required>
            <InputLabel htmlFor="maxGrade">모집 최고 학번</InputLabel>
            <Select
              label="모집 최고 학번"
              name="maxGrade"
              id="maxGrade"
              value={maxGrade !== null ? maxGrade : NO_LIMIT}
              onChange={handleNullValueChange}
              MenuProps={MenuProps}
            >
              <MenuItem value={NO_LIMIT}>무관</MenuItem>
              {univEnteredYears.map(option => (
                <MenuItem key={option.value} value={option.value}>
                  {option.value} 학번
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={6} sm={4} md={2} display="flex" justifyContent="center">
          <LocalizationProvider dateAdapter={AdapterDayjs}>
            <MobileDateTimePicker
              label="모집 시작일"
              inputFormat={'YYYY-MM-DD HH:mm'}
              mask={'____-__-__ __:__'}
              value={startAt}
              onChange={(date: Date | null) => {
                if (date) {
                  handleChange('startAt', date)
                }
              }}
              renderInput={params => (
                <TextField sx={{ width: '100%', paddingLeft: 0.5, paddingRight: 0.5 }} size="small" {...params} />
              )}
            />
          </LocalizationProvider>
        </Grid>
        <Grid item xs={6} sm={4} md={2} display="flex" justifyContent="center">
          <LocalizationProvider dateAdapter={AdapterDayjs}>
            <MobileDateTimePicker
              label="모집 종료일"
              value={endAt}
              inputFormat={'YYYY-MM-DD HH:mm'}
              mask={'____-__-__ __:__'}
              onChange={(date: Date | null) => {
                if (date) {
                  handleChange('endAt', date)
                }
              }}
              renderInput={params => (
                <TextField sx={{ width: '100%', paddingLeft: 0.5, paddingRight: 0.5 }} size="small" {...params} />
              )}
            />
          </LocalizationProvider>
        </Grid>
        <Grid item xs={12} sm={4} md={2} display="flex" justifyContent="center">
          <TextField
            sx={{ width: '100%', paddingLeft: 0.5, paddingRight: 0.5 }}
            size="small"
            label="예상 선발 인원"
            name="limit"
            id="limit"
            value={limit}
            type="number"
            inputProps={{ min: 0, max: 999 }}
            onChange={e => handleChange(e.target.name, parseInt(e.target.value))}
          />
        </Grid>
      </Grid>
    </Grid>
  )
}

export default RecruitInfo
